import { Component, OnInit } from '@angular/core';
import { Subject } from 'rxjs';
import {HttpService} from '../http.service';

@Component({
  selector: 'app-headlines',
  templateUrl: './headlines.component.html',
  styleUrls: ['./headlines.component.scss']
})
export class HeadlinesComponent implements OnInit {

  fsSubject = new Subject();
  fsValue = 'fs-11';

fsChanged(event) {
  this.fsSubject.next((event.target.value || 11));
}
   mArticles: Array<any>;
  mSources: Array<any>;

  constructor(private Nhttp:HttpService) {
    this.fsSubject.subscribe((value:any) => {
      this.fsValue = ~value.indexOf('fs-') ? value : `fs-${value}`;
    })
   }

  ngOnInit(): void {
    /*
    this.Nhttp.getBeer().subscribe(data => {
      this.brews = data
      */
      console.log(this.mArticles);
      
    
     this.Nhttp.initArticles().subscribe(data => this.mArticles = data['articles']);
    
    this.Nhttp.initSources().subscribe(data=> this.mSources = data['sources']);  
   
  }

}
